import React, { Component } from 'react';

class Results extends Component {
    constructor( props ){
        super( props );
        this.store = this.props.store;
    }

    votesAngularInPercent () {
        if ( this.store.getState().angular ) {
            return ( this.store.getState().angular /
             (this.store.getState().angular 
             + this.store.getState().react 
             + this.store.getState().vuejs) ) * 100 
        } 
        else {
            return 0
        }
    }

    votesReactInPercent () {
        console.log( this.store.react );
        if ( this.store.getState().react ) {
            return ( this.store.getState().react /
             (this.store.getState().angular 
             + this.store.getState().react 
             + this.store.getState().vuejs) ) * 100 
        } 
        else {
            return 0
        }
    }

    votesVuejsInPercent () {
        if ( this.store.getState().vuejs ) {
            return ( this.store.getState().vuejs / (this.store.getState().angular + this.store.getState().react + this.store.getState().vuejs) ) * 100 
        } 
        else {
            return 0
        }
    }

    angularPercentBar() {
        return {
            width: this.votesAngularInPercent() + '%'
        }
    }

    reactPercentBar() {
        return {
            width: this.votesReactInPercent() + '%'
        }
    }

    vuejsPercentBar() {
        return {
            width: this.votesVuejsInPercent() + '%'
        }
    }



    
    
    render() {
        return (
            <div>
                <span className="label label-danger badge-danger"> Angular: {this.votesAngularInPercent().toFixed(2) +'%' }</span>
                <div className="progress stripted active">
                    <div className="progress-bar progress-bar-striped bg-danger" role="progressbar"  style={ this.angularPercentBar() }>
                    </div>
                </div>

                <span className="label label-info badge-info"> React: {this.votesReactInPercent().toFixed(2) + '%' }</span>
                <div className="progress stripted active">
                    <div className="progress-bar progress-bar-striped bg-info" role="progressbar"  style={this.reactPercentBar() }>
                    </div>
                </div>

                <span className="label label-danger badge-success"> Vuejs: {this.votesVuejsInPercent().toFixed(2) + '%' }</span>
                <div className="progress progress-striped active">
                    <div className="progress-bar progress-bar-striped bg-success" role="progressbar" style={ this.vuejsPercentBar() }>
                    </div>
                </div>
            </div>
        )
    }
}

export default Results;


